@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,700;1,400&display=swap);
html{font-family:"Roboto", sans-serif;background-color:#85ffbd;background-image:linear-gradient(89deg, blue 0%, #c1dbe7 100%);background-size:contain}.App{padding:30px;text-align:center}

.stats{display:flex;justify-content:center}.stats__item{padding:10px 30px;font-size:18px}

.Comment:not(:last-child){border-bottom:1px solid blue}.Comment__header{font-weight:700}.Comment__header::first-letter{text-transform:capitalize}.Comment__text::first-letter{text-transform:capitalize}.Comment__email{color:black;font-style:italic;text-decoration:none}

.CommentList{margin-top:20px;padding:10px 10px 20px;font-size:0.8em;background-color:#cbcfdb;border-radius:30px;box-shadow:4px 4px rgba(2,1,1,0.4)}

.User__name{margin-bottom:10px;font-size:1.2em;font-weight:700}.User__email{margin:0;font-style:italic;text-decoration:none;color:black}.User__address{margin:0}

.Post{padding:30px 20px 20px;text-align:start;background-color:white;border-radius:30px;box-shadow:4px 4px rgba(32,30,160,0.6)}.Post__header{line-height:1.2em}.Post__header::first-letter{font-size:1.75em;text-transform:capitalize}.Post__text::first-letter{text-transform:capitalize}

.PostList{display:grid;grid-template-columns:repeat(3, 1fr);grid-gap:30px}@media (max-width: 920px){.PostList{grid-template-columns:1fr 1fr}}@media (max-width: 680px){.PostList{grid-template-columns:1fr}}

